<template>
  <div>
    <div class="dytuijian"> 
      <div class="lieBiao" v-for="lie in LieShu" :key="lie">
        <div style="height: 210px; width: 130px" v-for="item in Show[lie-1]" :key="item.createTime" class="list">
          <router-link :to="`/detail?id=${item.vod_id}`" class="vod_a" target="_blank">
          <div style="height: 190px; width: 130px">
             <img class="videoimg" :src="item.vod_pic" :alt="item.vod_name" :title="item.vod_name">
          </div>
          <div class="title_div">
            <div class="vname">{{ item.vod_name }}</div>
          </div>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "MinListVideo",
  data() {
    return {
      showList:[],
    };
  },
  props:['List','LieShu'],
  methods: {

  },
  computed:{
    Show(){
      let kong=[];
      let thatlist=this.List
      let num1=Math.ceil(thatlist.length/5)
      let num2=0;
      
      for(let i=0; i<num1;i++){
          //  console.log("i:",i,"num2",num2);
          kong[i]=thatlist.slice( num2,num2+7);
          num2+=7;
      }
      return kong;
    }
  }


};
</script>

<style scoped>
.dytuijian{
  margin-top: 15px;
}
.title {
  margin-left: 9.5vw;
  font-size: 20px;
  color: #333;
}
