  border: 1px solid var(--br);
  margin-right:5px ;
  margin-bottom: 5px;
}
.lieBiao{
  display: flex;
  justify-content:left;
  flex-wrap:wrap;
}
.videoimg{
  height:190px;
  width: 100%;
}
.title_div{
  padding: 2px;
  color: rgb(70, 70, 70);
  width:130px;
 
}
.vname{
  font-size: 13px;
  
}
.vname{
  color: #999;  
  width: 95px;
