<template>
  <div>
    <!-- <p>DetailVideo 详情页面</p> -->
    <el-container>
      <el-main>
        <el-row :gutter="10">
          <el-col :span="8">
            <div v-show="vod.vod_pic" >
              <img class="vodImg" :src="vod.vod_pic" alt="vod.vod_name" />
            </div>
          </el-col>
          <el-col :span="16">
            <div class="vod_jieshao" v-show="vod.vod_name" >
              <h1 class="vod_title">
                {{ vod.vod_name 
                }}<span class="vod_year" v-show="vod.vod_year!='0'">({{ vod.vod_year }})</span>
              </h1>
              <div class="otherbox" >
                当前为<em>{{ vod.vod_remarks }}</em
                >资源，最后更新于<em>4天前</em>
              </div>
              <div>
                <span>导演：</span
                ><a href="javascript:;" target="_blank">{{
                  vod.vod_director
                }}</a>
              </div>
              <div>
                <span>主演：</span
                ><a href="javascript:;" target="_blank">{{
                  vod.vod_actor ? vod.vod_actor : "未知"
                }}</a>
              </div>
              <div>
                <span>类型：</span
                ><a href="javascript:;" target="_blank">{{ vod.vod_class }}</a>
              </div>
              <div>
                <span>地区：</span
                ><a href="javascript:;" target="_blank">{{ vod.vod_area }}</a>
              </div>
              <div>
                <span>语言：</span
                ><a href="javascript:;" target="_blank">{{ vod.vod_lang }}</a>
              </div>
              <div><span>时间：</span>{{ vod.vod_pubdate ? vod.vod_pubdate : timeFormat }}</div>
              <div>
                <span>片长：</span>{{ vod.vod_duration ? vod.vod_duration : "未知" }}
              </div>
              <div>
                <span>又名：</span>{{ vod.vod_en ? vod.vod_en : "未知" }}
              </div>
              <div ><span>点击：</span>{{ vod.vod_hits }}</div>
              <div><span>评分：</span>{{ vod.vod_score }}</div>
              <!-- {{vod}} -->
            </div>
          </el-col>
          <el-col :span="24">
            <h2 class="jieshao2">电影介绍</h2>
            <p class="vod_cont">
              &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ vod.vod_content }}
            </p>
          </el-col>
          <!-- <el-col :span="24">
            <h2 class="jieshao2">资源下载</h2>
            <p class="vod_cont"></p>
          </el-col> -->
          <el-col :span="24">
            <h2 class="jieshao2">在线播放</h2>
              <el-col :span="24" class="moreAnniu">
                <div v-for="(item,index) in BofangList" :key="item.Url+index+item.epi"  class="anniu_Col">
                   <router-link :to="`/vplayer?id=${vod.vod_id}&url=${item.Url}`" class="vod_a" target="_blank">
                    <el-button size="small">{{item.epi}}</el-button>
                  </router-link>
                </div>
              </el-col>
          </el-col>
          <el-col :span="24">
            <h2 class="jieshao2">电影推荐</h2>
            <MinListVideo :List="aboutList" :LieShu="1"></MinListVideo>
          </el-col>

          <el-col :span="24">
            <h2 class="jieshao2">评论</h2>
            <el-input
              style="width: 99%"
              type="textarea"
              :rows="3"
              placeholder="请输入内容"
              v-model="userComment"
            >
            </el-input>
          </el-col>
          <el-col :span="2" :offset="22">
            <el-button
              class="anniuYouce"
              type="default"
              @click="sendComment"
              size="small"
              style="margin-top: 10px"
              >评论</el-button
            >
          </el-col>

          <el-col :span="24" class="com">
            <div class="pl" v-for="item in comment_list" :key="item.id">
              <div>
                <img class="tx" :src="item.comment_pic" alt="头像" />
                <span class="user_name">{{ item.comment_name }}</span>
                <span class="user_time">{{ item.comment_time }}</span>
              </div>
              <div>
                <p class="user_com">{{ item.comment_content }}</p>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-main>

      <el-aside width="300px" class="asideStyle">
        <h2 class="jieshao2">电影推荐</h2>
        <TUijian :ListData="hotList"></TUijian>
      </el-aside>
    </el-container>
  </div>
</template>

<script>
import service from "../../utils/request";
import MinListVideo from "./MinListVideo";
import TUijian from "./Tuijian";
export default {
  name: "DetailVideo",
  components: { MinListVideo, TUijian },
  data() {
    return {
      userComment: "", //评论
      vod: {
        vod_id: "",
        vod_name: "",
        vod_pic: "",
        vod_year: "",
        vod_class: "",
        vod_area: "",
        vod_lang: "",
        vod_score: "",
        vod_en: "",
        vod_blurb: "",
        vod_content: "",
        vod_remarks: "",
        vod_hits: "",
        vod_time: "",
        vod_director: "",
        vod_actor: "",
        vod_pubdate:"",
      },   //电影信息
      hotList: [], //热榜列表
      aboutList: [],  // 相关列表
      
      comment_list: [
        {
          comment_id: "00123",
          comment_pic: "https://img.mypianku.net/tx/Array.png",
          comment_name: "测试",
          comment_time: "1638763842",
          comment_content: "测试",
        },
       
      ],// 评论列表
      BofangList:[], //播放地址
    };
  },
  methods: {
    dateFormat(value, gs) {
      let date = new Date(parseInt(value) * 1000);
      let y = date.getFullYear();
      let m = date.getMonth() + 1;
      m = m < 10 ? "0" + m : m;
      let d = date.getDate();
      d = d < 10 ? "0" + d : d;
      let h = date.getHours();
      h = h < 10 ? "0" + h : h;
      let minute = date.getMinutes();
      let second = date.getSeconds();
      minute = minute < 10 ? "0" + minute : minute;
      second = second < 10 ? "0" + second : second;
      let yms = y + "年" + m + "月" + d + "日 ";
      let hms = h + ":" + minute + ":" + second;

      if (gs) {
        return yms + hms;
      } else {
        return yms;
      }
    },  //时间格式化
    sendComment() {
      if (this.userComment) {
        console.log(this.userComment);
        this.$message({
          message: "评论发送成功！",
          type: "success",
        });
        // 发送
      } else {
        this.$message.error("评论不为空!");
        //提示为空
        return;
      }
    },  // 发送评论
    GsUrl(urls){
      // 处理 pgutv_all 格式播放
      let aRr2= urls.split(/[$#]/);
      let arrayUrl=[]
      for(let i=0;i<aRr2.length;i++){
        if(aRr2[i] && aRr2[i+1]){
          let vote = {};
          vote.epi = aRr2[i];
          vote.Url = aRr2[i+1];
          arrayUrl.push(vote);
         }
         i++; 
      }
      console.log(arrayUrl);
      this.BofangList=arrayUrl;
     
    },// 地址处理
  },
  computed: {
    timeFormat() {
      return this.dateFormat(this.vod.vod_time);
    },
  },
  mounted() {
    // 获取数据
    // console.log(this.$route.query.id);
    // 当前电影
    service.get("/api/id", {
        params: {
          id: this.$route.query.id,
        },
      }).then((result) => {
        this.vod = result.data[0];  //设置视频属性
        document.title= result.data[0].vod_name;  //设置title
        let type = this.vod.type_id; //保存 type_id
        let type_1 = this.vod.type_id_1; 
        this.GsUrl(this.vod.vod_play_url);  //显示播放列表
        console.log("类型", type, type_1);
        // about
        service.get("/api/about", {
            params: {
              type_id: type,
            },
          }).then((result) => {
            console.log("about",result.data);
            this.aboutList = result.data;
          }).catch((err) => {
            console.log(err);
          });

    
        // hot
        service.get("/api/hot", {
            params: {
              type_id_1: type,
            },
          }).then((result) => {
            console.log('进入hotlist',result.data);
            this.hotList = result.data;
          }).catch((err) => {
            console.log(err);
          });
      })
      .catch((err) => {
        console.log(err);
      });
    // 处理数据

    for (let i = 0; i < this.comment_list.length; i++) {
      // 设置格式
      this.comment_list[i].comment_time = this.dateFormat(
        this.comment_list[i].comment_time,
        "yes"
      );
    }
  },
};
</script>

<style scoped>
.el-container {
  width: 80%;
  margin: 0 auto;
}
.el-aside {
  margin-top: 10px;
}
.el-main {
  margin-top: 10px;
}

.vodImg {
  height: 390px;
  width: auto;
  max-width: 275px;
}
.vod_jieshao div {
  margin-bottom: 5px;
  line-height: 1.5;
}
.vod_jieshao a {
  color: #337ab7;
  text-decoration: none;
}
.vod_jieshao {
  font-size: 13px;
}
.vod_title {
  font-size: 22px;
  font-weight: bolder;
  color: green;
  line-height: 1.5;
  margin: 0 0 0 0;
  display: block;
}
.vod_year {
  color: #333;
}
.otherbox {
  margin-top: 5px;
  color: green;
  font-size: 12px;
  background: #e7efff;
  display: inline-block;
  padding: 5px 8px 5px;
  border-radius: 3px;
  line-height: 1.5;
}
.otherbox em {
  padding: 0 5px;
}
.jieshao2 {
  font-size: 16px;
  line-height: 1.4;
  padding: 2px 0 2px 0px;
  font-weight: 500;
  margin: 20px 15px 10px 0;
}
.vod_cont {
  font-size: 14px;
  line-height: 1.5;
}
.tx {
  margin: 0;
  padding: 0;
  width: 36px;
  height: 36px;
  border-radius: 50%;
}
.com {
  margin: 20px 0 0 5px;
}
.user_name {
  font-size: 16px;
  line-height: 36px;
  margin: 10px 0 0 10px;
  padding: 10px 0 20px 0px;
  height: 36px;
  color: #99cc66;
}
.user_time {
  font-size: 14px;
  float: right;
  color: #333;
}
.user_com {
  margin: 0 0 0 45px;
  font-size: 14px;
  color: #333;
}
.moreAnniu{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}
.anniu_Col{
  margin-top: 10px;
  margin-right: 5px;
}
.anniu_Col a {
  color: #333;
  text-decoration: none;
}
</style>